<template>
  <div v-if="visible" class="common-modal">
    <div class="mask"></div>
    <div class="box">
      <div class="ani animate__animated animate__bounceIn">
        <div class="box-bg-top"></div>
        <div class="box-bg-bottom"></div>
        <div class="main">
          <div class="main-title align-center justify-center">
            <div class="text align-center justify-center">
              {{ title }}
            </div>
            <span @click="hideModal" class="close"></span>
          </div>
          <div class="main-content">
            <slot name="content"></slot>
            <slot name="footer"></slot>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonModal',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '提示'
    }
  },
  data () {
    return {}
  },
  methods: {
    showModal () {
      this.visible = true
    },
    hideModal () {
      this.$emit('update:visible', false)
    }
  }
}
</script>

<style lang="less" scoped>
.common-modal {
  z-index: 4;
  position: fixed;
  z-index: 5;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  height: 100vh;
  width: 100vw;
  .mask {
    position: fixed;
    z-index: 5;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .box {

    position: fixed;
    z-index: 6;
    width: 610px;

    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    .ani {
      box-sizing: border-box;
      padding: 88px 0 40px;
      .box-bg-top {
        position: absolute;
        top: 0;
        background-image: url(~@/assets/images/common/modal_bg_top.png);
        background-repeat: no-repeat;
        background-size: 100%;
        width: 100%;
        height: 88px;
      }
      .box-bg-bottom {
        position: absolute;
        bottom: 0;
        background-image: url(~@/assets/images/common/modal_bg_bottom.png);
        background-repeat: no-repeat;
        background-size: 100%;
        width: 100%;
        height: 45px;
      }

      .main {
        background-color: #fff;
        min-height: 200px;
        border-left: 4px solid #feeebc;
        border-right: 4px solid #feeebc;
        .main-title {
          height: 88px;
          width: 100%;
          position: absolute;
          top: 0;
          color: #ffe29d;
          font-size: 40px;
          .text {
            &::before,
            &::after {
              content: '';
              width: 46px;
              height: 12px;
              display: inline-block;
              background-image: url(~@/assets/images/common/modal_title_arrow.png);
              background-size: 100%;
              background-repeat: no-repeat;
            }
            &::before {
              margin-right: 30px;
            }
            &::after {
              margin-left: 30px;
              transform: rotate(180deg);
            }
          }
          .close {
            position: absolute;
            right: 10px;
            background-image: url(~@/assets/images/common/modal_close.png);
            background-repeat: no-repeat;
            background-position: center;
            background-size: 28px 28px;
            width: 88px;
            height: 88px;
          }
        }
        .main-content {
          padding: 60px 30px 20px;
          text-align: center;
          max-height: 80vh;
          overflow-y: auto;
          .text {
            font-size: 32px;
            color: #333;
            line-height: 1.5;
          }
          .sub-text {
            font-size: 24px;
            color: #666;
          }
          .modal-footer {
            img {
              width: 348px;
              height: 128px;
            }
          }
          .modal-footer-row-two {
            display: flex;
            img {
              width: 278px;
              height: 128px;
            }
          }
        }
      }
    }
  }
}
</style>



