// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'
import store from './store'
import 'mint-ui/lib/style.css'
import './assets/css/reset.css'
import 'animate.css'
import './assets/css/common.less'
import '../static/config'
import request from '@/utils/request'

import Lottie from 'lottie-web'

import { Progress, Header, Button, Toast, Field, Indicator, InfiniteScroll, Spinner } from 'mint-ui'

import Modal from '@/components/commonModal'
import Pop from '@/components/commonPop'
import Divider from '@/components/divider'
import Share from '@/components/share'

Vue.component(Progress.name, Progress)
Vue.component(Header.name, Header)
Vue.component(Button.name, Button)
Vue.component(Field.name, Field)
Vue.component(Spinner.name, Spinner)
Vue.component('Modal', Modal)
Vue.component('Pop', Pop)
Vue.component('Divider', Divider)
Vue.component('Share', Share)

// import MintUI from 'mint-ui'
// Vue.use(MintUI)
Vue.use(Lottie)
Vue.use(InfiniteScroll)
Vue.prototype.lottie = Lottie
Vue.prototype.Toast = Toast
Vue.prototype.Indicator = Indicator
Vue.prototype.gconfig = window.config
Vue.prototype.$request = request

Storage.prototype.setExpire = (key, value, expire) => {
  let obj = {
    data: value,
    time: Date.now(),
    expire: expire || 1000 * 60 * 60 * 6
  }
  localStorage.setItem(key, JSON.stringify(obj))
}

Vue.config.productionTip = false
// console.log('window.gconfig in main.js')
// console.log(window.gconfig)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  components: { App },
  template: '<App/>'
})



