import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

export default new Vuex.Store({
  namespaced: true,
  state: {
    token: 'xxxxxx',
    nickname: '爱丽丝alice',
    // nickname: localStorage.getItem('nickname'),
    firstCome: true,
    isNewer: true,
    userInfo: {
      uid: '',
      cid: '',
      nick_name: '',
      star: 0,
      mobile: '',
      'fund_account': '',
      levelCount0: 10,
      levelCount1: 10,
      levelCount2: 10,
      icon_type: '',
      client_id: '',
      'lable': null,
      'role': '0',
      'leveal': '0',
      'total': 0,
      'available': 0

    }

  },
  mutations: {
    setNickname (state, payload) {
      state.isNewer = false
      if (payload.nick_name) {
        state.nick_name = payload.nick_name
        state.userInfo.nick_name = payload.nick_name
      }
    },
    setUserInfo (state, payload) {
      // state.userInfo = payload

      // debugger
      // 昵称
      if (payload.nick_name) {
        state.userInfo.nick_name = payload.nick_name
      }
      if (payload.uid) {
        state.userInfo.uid = payload.uid
      }
      if (payload.cid) {
        state.userInfo.cid = payload.cid
      }
      if (payload.mobile) {
        state.userInfo.mobile = payload.mobile
      }
      if (payload.star) {
        state.userInfo.star = payload.available
      }
      if (payload.role) {
        state.userInfo.role = payload.role
      }
      // 级别，头像背景
      if (payload.leveal) {
        state.userInfo.leveal = payload.leveal
      }
      // 累计获得星钻数
      if (payload.total) {
        state.userInfo.total = payload.total
      }
      if (payload.available) {
        state.userInfo.available = payload.available
      }
      if (payload.icon_type) {
        state.userInfo.icon_type = payload.icon_type
      }
      if (payload.client_id) {
        state.userInfo.client_id = payload.client_id
      }
      if (payload.fund_account) {
        state.userInfo.fund_account = payload.fund_account
      }
      if (payload.levelCount0) {
        state.userInfo.levelCount0 = payload.levelCount0
      }
      if (payload.levelCount1) {
        state.userInfo.levelCount1 = payload.levelCount1
      }
      if (payload.levelCount2) {
        state.userInfo.levelCount2 = payload.levelCount2
      }

      localStorage.setExpire('userInfo', state.userInfo)
      // console.log('store:')
      // console.log(JSON.parse(localStorage.getItem('userInfo')).data)
    },
    modifyCount (state, payload) {
      if (payload.num && payload.level === 0) {
        state.levelCount0 += payload.num
      } else if (payload.num && payload.level === 1) {
        state.levelCount1 += payload.num
      } else if (payload.num && payload.level === 2) {
        state.levelCount2 += payload.num
      }
    },
    modifyStar (state, payload) {
      if (payload.num) {
        state.star += payload.num
      }
    }
  },
  actions: {},
  modules: {},
  getters: {
    isNewer: state => state.isNewer,
    nickname: state => state.nickname,
    userInfo: state => state.userInfo
  }
})



