import Vue from 'vue'
import Router from 'vue-router'
// import HelloWorld from '@/pages/HelloWorld'

Vue.use(Router)

export default new Router({
  routes: [
    // {
    //   path: '/',
    //   name: 'HelloWorld',
    //   component: HelloWorld
    // }
    {
      path: '/',
      name: 'loading',
      component: () => import('@/pages/loading')
    },
    {
      path: '/header',
      name: 'header',
      component: () => import('@/components/header')
    },
    {
      path: '/guide',
      name: 'guide',
      component: () => import('@/pages/guide')
    },
    {
      path: '/dream',
      name: 'dream',
      component: () => import('@/pages/dream')
    },
    {
      path: '/home',
      name: 'home',
      component: () => import('@/pages/home'),
      meta: {
        keepAlive: true,
        title: '信纪元'
      }
    },
    {
      path: '/userset',
      name: 'userset',
      component: () => import('@/pages/userSet')
    },
    {
      path: '/usersetname',
      name: 'usersetname',
      component: () => import('@/pages/userSetName')
    },
    {
      path: '/strategy',
      name: 'strategy',
      component: () => import('@/pages/strategy'),
      meta: {
        title: '活动攻略'
      }
    },
    {
      path: '/invitationList',
      name: 'invitationList',
      component: () => import('@/pages/invitationList'),
      meta: {
        title: '呼朋唤友记录'
      }
    },
    {
      path: '/invitationSortList',
      name: 'invitationSortList',
      component: () => import('@/pages/invitationSortList'),
      meta: {
        title: '唤友榜单'
      }
    },
    {
      path: '/luckyTurn',
      name: 'luckyTurn',
      component: () => import('@/pages/luckyTurn')

    },
    {
      path: '/jsondemo', // json 动画 demo
      name: 'jsondemo',
      component: () => import('@/pages/jsonAnimationDemo')
    },
    {
      path: '/coupon',
      name: 'coupon',
      component: () => import('@/pages/coupon')
    },
    {
      path: '/test',
      name: 'test',
      component: () => import('@/pages/test')
    },
    {
      path: '/shop',
      name: 'shop',
      component: () => import('@/pages/shop')
    },
    {
      path: '/meeting',
      name: 'meeting',
      component: () => import('@/pages/meeting')
    },
    {
      path: '/digitalCollection',
      name: 'digitalCollection',
      component: () => import('@/pages/digitalCollection')
    },
    {
      path: '/digitalShop',
      name: 'digitalShop',
      component: () => import('@/pages/digitalShop')
    },
    {
      path: '/digitalMyCollection',
      name: 'digitalMyCollection',
      component: () => import('@/pages/digitalMyCollection')
    },
    {
      path: '/digitalDetail',
      name: 'digitalDetail',
      component: () => import('@/pages/digitalDetail'),
      meta: {
        keepAlive: true
      }
    },
    {
      path: '/digitalShare',
      name: 'digitalShare',
      component: () => import('@/pages/digitalShare')
    },
    {
      path: '/dialogue',
      name: 'dialogue',
      component: () => import('@/pages/dialogue')
    },
    {
      path: '/personalPage',
      name: 'personalPage',
      component: () => import('@/pages/personalPage')
    },
    {
      path: '/login',
      name: 'login',
      component: () => import('@/pages/login')
    }

  ]
})



