import axios from 'axios'
import { Toast } from 'mint-ui'
// import config from '@/utils/config'

// console.log('window.gconfig')
// console.log(window.gconfig)

let baseURL = '/'
// 判断开发环境（处理本地代理）
if (process.env.NODE_ENV === 'development') { // 开发环境
  baseURL = '/metaapi/' + window.gconfig.API_SERVICE
  localStorage.setItem('baseurl', 'dev')
} else { // 编译环境
  localStorage.setItem('baseurl', 'prod')
  localStorage.setItem('baseurl', window.gconfig.API_URL + window.gconfig.API_SERVICE)
  baseURL = window.gconfig.API_URL + window.gconfig.API_SERVICE // 在这里使用配置文件中的域名
}
console.log(baseURL)

const instance = axios.create({
  baseURL: baseURL,
  // baseURL: config.baseUrl || '/',
  timeout: 60000,
  responseType: 'json',
  withCredentials: false // 是否允许带cookie这些
})

// 请求拦截
instance.interceptors.request.use(
  config => {
    if (config.method === 'get') {
      config.params &&
        Object.keys(config.params).forEach(item => {
          if (
            config.params[item] === null ||
            config.params[item] === undefined
          ) {
            config.params[item] = ''
          }
        })
    } else {
      config.data &&
        Object.keys(config.data).forEach(item => {
          if (config.data[item] === null || config.data[item] === undefined) {
            config.data[item] = ''
          }
        })
    }
    if (config.requestConfig && config.requestConfig.timeout) {
      config.timeout = config.requestConfig.timeout
    }
    return config
  },
  error => {
    Toast(error)
    return Promise.reject(error)
  }
)

// 响应拦截
instance.interceptors.response.use(
  response => {
    if (response.data.error_no === '0') {
      return response.data
    } else {
      if (
        !response.config.requestConfig ||
        !response.config.requestConfig.noErrorMsg
      ) {
        const msg = response.data.error_info || '系统错误'
        Toast(msg)
      }
      return Promise.reject(response.data)
    }
  },
  error => {
    if (error && error.response) {
      const data = error.response.data
      Toast(data.error_info)
    }
    if (error.code === 'ECONNABORTED' || error.message.includes('timeout')) {
      Toast('请求超时')
    }
    return Promise.reject(error)
  }
)

/**
 *
 * @param {String} method 请求方法(get|post|put|patch|delete...)
 * @param {String} url 请求地址
 * @param {Object} data 请求数据,key/value格式
 * @param {Object} requestConfig 请求配置，（noErrorMsg:错误请求是否展示提示;）
 * @returns {Promise} 响应
 * @示例
 * await this.$request('get', `/dms/station/detail/${this.id}`, data, {noErrorMsg:false})
 * await this.$request({url:xxx, data:xxx, method:xxx, requestConfig:xxx})
 */
const request = (method, url, data, requestConfig) => {
  if (typeof method === 'object') {
    const config = method
    url = config.url || '/'
    data = config.data || {}
    method = config.method || 'post'
    requestConfig = config.requestConfig || null
  }
  const option = {
    url,
    method,
    requestConfig
  }
  if (/put|post|patch/i.test(method)) {
    option.data = data
  } else {
    option.params = data
  }
  return instance(option)
}

export default request



