<template>
  <div>
    <!-- 分享 -->
    <Pop class="pop" v-bind="$attrs" v-on="$listeners" title="分享到">
      <div class="content" slot="content">
        <div class="item" @click="doShare">
          <img src="~@/assets/images/common/icon_wechat@2x.png" />
          <div class="tip">微信好友</div>
        </div>
        <div class="item">
          <img src="~@/assets/images/common/icon_pyq@2x.png" />
          <div class="tip">朋友圈</div>
        </div>
        <div class="item">
          <img src="~@/assets/images/common/icon_kongjian@2x.png" />
          <div class="tip">QQ空间</div>
        </div>
        <div class="item">
          <img src="~@/assets/images/common/icon_qq@2x.png" />
          <div class="tip">QQ好友</div>
        </div>
      </div>
    </Pop>
  </div>
</template>
<script>

export default {
  name: 'Share',
  props: {},
  data () {
    return {
      userInfo: JSON.parse(localStorage.getItem('userInfo')).data,
      shareInfo: JSON.parse(localStorage.getItem('baseInfo')).data

    }
  },
  computed: {
    userAvator () {
      return `/static/img/user/head${this.userInfo.icon_type}@2x.png`
    }
  },
  watch: {
    $router () {
      console.log('attr', this.$attrs)
    }
  },
  methods: {
    async doShare () {
      let res = await this.$request('get', 'user/doshare', { uid: this.userInfo.uid })
      if (res && res.error_no === '0') {
        console.log(res.data)
        this.share()
      }
    },
    // 分享
    share () {
      console.log('this.shareInfo')
      console.log(this.shareInfo)
      // let fullUrl =
      //   'http://action:10055/?url=' +
      //   encodeURIComponent(this.shareInfo.shareUrl) +
      //   '&&title=' +
      //   encodeURIComponent(this.shareInfo.share_title) +
      //   '&&message=' +
      //   encodeURIComponent(this.shareInfo.shareContent)
      // window.location.href = fullUrl
    }
  }
}
</script>
<style lang="less" scoped>
.content {
  position: relative;
  padding: 16px 45px;
  min-height: 220px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  .item {
    width: 150px;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    > img {
      width: 100px;
      height: 100px;
    }
    .tip {
      font-size: 28px;
      color: #333;
    }
  }
}
</style>



