<template>
  <div v-if="visible" class="common-pop">
    <div class="mask"></div>
    <div class="box  animate__animated animate__bounceInUp">
      <div class="box-bg-top">
        <div class="main-title align-center justify-center">
          <div class="text align-center justify-center">
            {{ title }}
          </div>
          <span @click="hideModal" class="close"></span>
        </div>
      </div>
      <div class="main">
        <div class="main-content">
          <slot name="content"></slot>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'CommonPop',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    title: {
      type: String,
      default: '提示'
    }
  },
  data () {
    return {}
  },
  methods: {
    showModal () {
      this.visible = true
    },
    hideModal () {
      this.$emit('hidePop', false)
      this.$emit('update:visible', false)
    }
  }
}
</script>

<style lang="less" scoped>
.common-pop {
  z-index: 3;
  position: absolute;
  .mask {
    position: fixed;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    height: 100vh;
    width: 100vw;
    background-color: rgba(0, 0, 0, 0.5);
  }
  .box {
    box-sizing: border-box;
    position: fixed;
    width: 100%;
    padding: 288px 0 0;
    left: 0;
    bottom: 0;
    .box-bg-top {
      position: absolute;
      top: 0;
      background-image: url(~@/assets/images/mine/mine_pop_bg@2x.png);
      background-repeat: no-repeat;
      background-size: 100%;
      width: 100%;
      height: 288px;
      .main-title {
        height: 88px;
        width: 100%;
        position: absolute;
        bottom: 0;
        color: #ffe29d;
        font-size: 40px;
        .close {
          position: absolute;
          right: 10px;
          background-image: url(~@/assets/images/common/modal_close.png);
          background-repeat: no-repeat;
          background-position: center;
          background-size: 28px 28px;
          width: 88px;
          height: 88px;
        }
      }
    }

    .main {
      background-image: linear-gradient(#ff4829, #fd9e7a);
      background-color: #ff4829;
      padding: 0 16px 16px;
      border-left: 4px solid #feeebc;
      border-right: 4px solid #feeebc;

      .main-content {
        border-radius: 16px;
        background-color: #fef0f2;
        max-height: 80vh;
        overflow-y: auto;
      }
    }
  }
}
</style>



