<template>
  <!-- 分割线 -->
  <div v-show="visible" class="common-divider">
    <span class="line"></span>
    <span v-if="type === 'text'" class="text">{{ title }}</span>
    <mt-spinner v-else class="loadimg text" type="fading-circle"></mt-spinner>
    <!-- <slot name="content"></slot> -->
    <span class="line"></span>
  </div>
</template>

<script>
export default {
  name: 'CommonModal',
  props: {
    visible: {
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      default: 'spin'
    },
    title: {
      type: String,
      default: '已全部加载'
    }
  },
  data () {
    return {}
  },
  methods: {}
}
</script>

<style lang="less" scoped>
.common-divider {
  height: 40px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  .line {
    height: 2px;
    flex-grow: 1;
    background-color: #eee;
  }
  .text {
    margin: 0 10px;
    color: #888;
  }
}
</style>



