<template>
  <div id="app" class="app">
    <!-- <img src="./assets/logo.png"> -->
    <!-- <router-view /> -->
    <!-- <mt-header fixed title="信纪元" class="">
      <mt-button
        v-show="isShow"
        @click="handleBack"
        icon="back"
        slot="left"
      ></mt-button>
    </mt-header> -->
    <keep-alive>
      <router-view v-if="$route.meta.keepAlive"></router-view>
    </keep-alive>
    <router-view v-if="!$route.meta.keepAlive"></router-view>
  </div>
</template>

<script>
export default {
  name: 'App',
  data () {
    return {
      window: window,
      isShow: true
    }
  },
  watch: {
    // $route (now, old) { // 监控路由变换，控制返回按钮的显示
    //   if (now.path == '/home/home') {
    //     this.isShow = false
    //   } else {
    //     this.isShow = true
    //   }
    // }
  },
  methods: {
    handleBack () {
      // console.log(this.$route)
      if (this.$route.name !== 'loading') {
        this.$router.back()
      }
    }
  }
}
</script>

<style>
#app,
.app {
  width: 100%;
  height: 100%;
  /* padding-top: 60px; */
}

.mint-header {
  height: 60px;
  width: 100%;
  background: #fff;
  color: #333;
}
.mint-header-title {
  color: #333;
  font-size: 34px;
}
.mint-header .mintui {
  font-size: 28px;
}
.mint-header.is-fixed {
  z-index: 4;
}
</style>



